.App {
  text-align: center;
  min-height: 100%;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 5em;
  -ms-flex-item-align: center;
      align-self: center;
}

.App-header, .App-header__hidden {
  background-color: #222;
  color: white;
  max-height: 18em;
  -webkit-transition: max-height 2s;
  -o-transition: max-height 2s;
  transition: max-height 2s;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
}

.App-header__hidden {
  max-height: 0;
}

.App-header-content {
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.App-actionButtons {
  width: 10em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.App-processes, .App-parameters {
  padding: 1em 0 1em 0;
  display: -ms-flexbox;
  display: flex;
}

.App-processes {
  background-color: #aaa;
}

.App-parameters {
  background-color: #ddd;
}

.App-processList, .App-parameterList {
  -ms-flex-positive: 1;
      flex-grow: 1;
  font-size: large;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: baseline;
      align-items: baseline;
  -ms-flex-line-pack: start;
      align-content: flex-start;
}

.App-processList button.inactive {
  color: #999;
}

.App-text {
  -ms-flex-positive: 1;
      flex-grow: 1;
  overflow-y: scroll;
  border-width: 1px 0 0 0;
  padding: 1em;
}

.App-text:focus {
  outline:none;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



